@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
/* purgecss start ignore */

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.App {
  text-align: center;
}

/* PAGE COMPLETE */

.App-page {
  display: flex;
}

@media screen and (max-width: 480px) {
  .App-page {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 1025px) {
  .App-page {
    display: flex;
    flex-direction: column;
  }
}

/* HEADER */

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: white;
  height: 10rem;
  width: 100%;
}

@media screen and (max-width: 480px) {
  .App-header {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    color: white;
    height: 5rem;
    width: 100%;
  }
}

/* MENU */

.App-menu {
  letter-spacing: 0.2rem;
  background: #ffffff;
  font-size: 1.4rem;
  min-width: 350px;
  text-align: left;
  margin-left: 1rem;
  height: calc(100vh - 10rem);
  overflow-y: auto;
  border-right: 1px solid black;
}

.App-menu h2 {
  font-size: 1.7rem;
}

a {
  text-decoration: none;
  color: hsl(0, 100%, 100%);
}

p a {
  color: #aca5cf;
}

@media screen and (max-width: 480px) {
  .App-menu {
    letter-spacing: 0.2rem;
    background: #ffffff;
    font-size: 1.4rem;
    text-align: center;
    margin-left: 1rem;
    height: 15rem;
    overflow-y: scroll;
    border-right: 1px solid black;
  }
}

@media screen and (max-width: 1025px) {
  .App-menu {
    letter-spacing: 0.2rem;
    background: #ffffff;
    font-size: 1.4rem;
    text-align: center;
    margin-left: 1rem;
    height: 20rem;
    overflow-y: scroll;
    border-right: 1px solid black;
  }
}

/* CONTENU */

.App-content {
  text-align: left;
  width: 100%;
  height: 30rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}

.App-content div {
  text-align: center;
}

.App-content h3 {
  margin: 2rem;
}

.App-content pre {
  text-align: left;
  display: inline-block;
  margin-top: -1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  padding: 2rem;
  border: 3px solid #333333;
  border-radius: 2% 6% 5% 4% / 1% 1% 2% 4%;
  letter-spacing: 0.3ch;
  background: #ffffff;
  position: relative;
}

@media screen and (max-width: 480px) {
  .App-content {
  }

  .App-content pre {
  }
}

@media screen and (max-width: 1025px) {
  .App-content {
  }

  .App-content pre {
  }
}

/* purgecss end ignore */

